import React from 'react'
import { NavLink } from 'react-router-dom'


interface IInterface {
  title: string
  to: any
}

const NavItem = (props: IInterface): React.ReactElement => {
  return (
    <li className="nav-item">
      <NavLink to={props.to} activeClassName="active" className="nav-link">
        {props.title}
      </NavLink>
    </li>
  )
}

export default NavItem
